<template>
  <div class="shopcart">
    <van-nav-bar
      title="我的购物车"
      :right-text="edit.title"
      @click-right="editShopcart"
    />

    <div class="shopcart-box">

      <div class="item-box">
        <van-row class="item" v-for="(item, index) in shopcartData" :key="index">
          <van-col class="item-checkbox" span="2">
            <van-checkbox v-model="item.isCheck" @change="simpleSelect"></van-checkbox>
          </van-col>
          <van-col span="19" offset="2">
            <van-row>
              <van-col span="8">
                <img class="auto-img" :src="item.url" alt="">
              </van-col>
              <van-col class="item-info" span="16">
                <div class="item-name">{{item.name}}</div>
                <div class="item-price" v-show="!edit.isEdit"><span class="price">{{item.price}}</span><span class="count">x{{item.count}}</span></div>
                <div v-show="edit.isEdit">
                  <van-stepper v-model="item.count" @input="changeCount" />
                </div>
              </van-col>
            </van-row>
          </van-col>
          <van-col span="3" class="item-delete" v-show="edit.isEdit" @click="simpleDelete(index)">删除</van-col>
        </van-row>
      </div>

    </div>


    <van-submit-bar
      class="submit"
      :price="submitInfo.price"
      button-text="提交订单"
      v-show="!edit.isEdit"
      @submit="commitOrder"
    >
    <van-checkbox v-model="submitInfo.allCheck" @click="allSelect">全选</van-checkbox>
    </van-submit-bar>

    <van-submit-bar
      class="submit all-delete"
      button-text="全部删除"
      v-show="edit.isEdit"
      @submit="allDelete"
    >
      <van-checkbox v-model="submitInfo.allCheck" @click="allSelect">全选</van-checkbox>
    </van-submit-bar>
    
  </div>
</template>

<style scoped lang="less">
  .shopcart{
    .submit{
      bottom: 1.33333rem;
    }

    .all-delete{
      .van-button{
        margin-left: auto;
      }
    }

    .item-box{
      .item{
        // height: 2.133rem;
        position: relative;
        padding: .267rem;
        margin-bottom: .133rem;
        background-color: #fff;
      }
      .item-checkbox{
        position: absolute;
        top: 50%;
        left: .267rem;
        transform: translateY(-50%);
      }
      .item-delete{
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        text-align: center;
      }
      .item-name{
        font-size: .32rem;
        color: #666;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .item-info{
        padding: 0 .133rem;
      }
      .item-price{
        margin-top: .133rem;
      }
      .price{
        font-size: .373rem;
        color: #e4393c;
        margin-right: .267rem;
      }
      .count{
        color: #666;
        font-size: .32rem;
      }
    }
  }
</style>

<script>
  import $ from 'jquery'
  export default {
    name: 'shopcart',

    data() {
      return {
        edit: {
          title: '管理',
          isEdit: false
        },
        submitInfo: {
          price: 0,
          allCheck: false
        },

        shopcartData: []

      };
    },

    created() {

      //提示
      this.$toast.loading({
        //当时间0时，提示框不会自动关闭
        duration: 0,
        message: '数据加载中...',
        //遮罩层
        // overlay: true
      });

      let self = this;
      
      //发起ajax请求
      $.ajax({
        type: 'GET',
        url: 'http://api.kangliuyong.com:9002/pros',
        data: {
          count: 5
        },
        success: function (data) {
          for (let i = 0; i < data.length; i++) {
            data[i].id = i + 1;
            data[i].isCheck = false;
            data[i].count = 1;
          }

          self.shopcartData = data.concat();

          

          //关闭提示框
          self.$toast.clear();
        }
      })

    },

    methods: {
      editShopcart() {
        this.edit.title = !this.edit.isEdit ? '完成' : '管理';
        this.edit.isEdit = !this.edit.isEdit;
      },

      //提交订单
      commitOrder() {
      
        //如果没有购买商品
        if (this.submitInfo.price == 0) {
          this.$toast('请选择购买商品');
          return;
        }

        //获取商品的id, 数量, 购买时间
        let order = [];
        let time = new Date().getTime();
        this.shopcartData.forEach(v => {
          if (v.isCheck) {
            let o = {
              id: v.id,
              count: v.count,
              time
            };

            order.push(o);
          }
        })

     
      },

      //计算总金额
      sum() {
        this.submitInfo.price = 0;

        //计算总金额
        this.shopcartData.forEach(v => {
          if (v.isCheck) {
            this.submitInfo.price += v.price * v.count * 100;
          }
        })
      },

      //全部删除
      allDelete() {
       
        this.shopcartData = [];

        this.submitInfo.allCheck = false;
        this.submitInfo.price = 0;
      },

      //删除单个商品
      simpleDelete(index) {
        this.shopcartData.splice(index, 1);

        this.sum();
      },

      //全选
      allSelect() {
       
        this.shopcartData.forEach(v => {
          v.isCheck = !this.submitInfo.allCheck;
        })

        this.sum();

      },

      //单选
      simpleSelect() {

        this.sum();

        for (let i = 0; i < this.shopcartData.length; i++) {
          if (!this.shopcartData[i].isCheck) {
            this.submitInfo.allCheck = false;
            return;
          }
        }

        this.submitInfo.allCheck = true;
      },

      //修改数量
      changeCount() {
        this.sum();
      }
    }
  }
</script>